$color-bg: #e4ebf5;

$color-default: #222;
$color-red: #d44439;
$color-gray: #666;
$color-light-gray: #999;
$color-deep-white: #e4e4e4;
$color-gold: #bba8a8;
$color-primary: #2979ff;
$color-primary-light: #82b1ff;

$font-size-default: 14px;
$font-size-max: 24px;
$font-size-bigger: 20px;
$font-size-large: 18px;
$font-size-normal: 16px;
$font-size-mini: 12px;

$padding-window: 20px;

@mixin fade {
  &:global(.fade-enter) {
    opacity: 0;
    transform: translateX(100%);
  }

  &:global(.fade-enter-active) {
    opacity: 1;
    transform: translateX(0);
    transition: all 500ms;
  }

  &:global(.fade-exit) {
    opacity: 1;
    transform: translateX(0);
  }

  &:global(.fade-exit-active) {
    opacity: 0;
    transform: translateX(-100%);
    transition: all 500ms;
  }
}

@mixin fly {
  &:global(.fly-enter),
  &:global(.fly-appear) {
    transform: translate3d(100%, 0, 0);
  }
  &:global(.fly-enter-active),
  &:global(.fly-appear-active) {
    transition: transform 0.3s;
    transform: translate3d(0, 0, 0);
  }
  &:global(.fly-exit-active) {
    transition: transform 0.3s;
    transform: translate3d(100%, 0, 0);
  }
}

.album-wrap {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 99;
  background-color: $color-bg;
  display: flex;
  flex-direction: column;
  &:global(.fly-enter),
  &:global(.fly-appear) {
    transform: translate3d(100%, 0, 0);
  }
  &:global(.fly-enter-active),
  &:global(.fly-appear-active) {
    transition: transform 0.3s;
    transform: translate3d(0, 0, 0);
  }
  &:global(.fly-exit-active) {
    transition: transform 0.3s;
    transform: translate3d(100%, 0, 0);
  }
  .header {
    display: flex;
    align-items: center;
    width: 100%;
    height: 50px;
    padding: 0 15px;
    font-size: large;
    color: $color-gray;
    .singer-name {
      margin-bottom: 0;
      margin-left: 10px;
    }
  }
  .detail-wrap {
    display: flex;
    padding: 15px 30px;
    align-items: center;
    justify-content: space-between;
    .detail-button {
      border-radius: 50%;
      color: $color-primary-light;
    }
    .img-card {
      border-radius: 50%;

      .content {
        display: flex;
        align-items: center;
        height: 182px;
        width: 182px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-clip: content-box;
        border-radius: 50%;
        background-position: top;
        background-size: cover;
      }
    }
  }
  .songs-wrap {
    padding: 8px;
  }
}
